<template>
  <transition name="scale">
    <div class="modal">
      <div class="modal__block">
        <h3 class="modal__title">
          {{ !edit ?  words.titlewindow[lang] :  words.titlewindowedit[lang] }}
        </h3>
        <div class="modal__inputs">
          <label>
            <span>Title</span>
            <input type="text" v-model="title" placeholder="Tile" />
          </label>
          <label>
            <span>Content</span>
            <input type="text" v-model="content" placeholder="Content" />
          </label>
        </div>
        <div class="modal__btns">
          <button class="modal__btn delete" @click="closeModal">{{words.closebtn[lang]}}</button>
          <button class="modal__btn redact" v-if="!edit" @click="addNote">{{words.addbtn[lang]}}</button>
          <button class="modal__btn redact" v-else @click = "changeNote" >{{words.editwindowbtn[lang]}}</button>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    edit: Boolean,
    currentId: Number,
    editNote: Object,
    lang:String,
  },
  inject: ['words'],
  data() {
    return {
      title: "",
      content: "",
      id: this.currentId,
    };
  },
  methods: {
    closeModal() {
      this.$emit("closeModal", false);
    },
    addNote() {
      if (this.title != "" && this.content != "") {
        const item = {
          id: this.id++,
          title: this.title,
          desc: this.content,
          data: new Date().toLocaleDateString(),
        }
        this.$emit('addNote', item)
        this.$emit('closeModal', false)
        this.title = ""
        this.content = ""
      }
    },
    changeNote() {
      if (this.title != "" && this.content != "") {
        const editedNote = {
          id: this.editNote.id,
          title: this.title,
          desc: this.content,
          data: new Date().toLocaleDateString(),
        }
        this.$emit("editedNote", editedNote)
        this.closeModal()
        this.title = ""
        this.content = ""
      }
    }
  },
};
</script>

