<template>
  <div class="notes">
    <div class="notes__top">
      <h2 v-if="notes.length == 0" class="notes__title">{{ words.noinfobar[lang] }}</h2>
      <h2 class="notes__title" v-else>{{ !search ? words.infobar[lang] : words.appbarseacrch[lang] }}</h2>
      <button class="notes__btn" @click="changer = !changer">
        <img src="@/assets/img/list.svg" alt="" v-if="changer" />
        <img src="@/assets/img/grid.svg" alt="" v-else />
        <span class="notes__btn-span">
          {{ changer ? words.list[lang] : words.grid[lang] }}
        </span>
      </button>
    </div>
    <div :class="[changer ? grid : flex]">
      <NotesItem v-for="note in notes" :key="note.id" :note="note" @changeNote="$emit('changeNote', note.id)"
        @delNote="$emit('delNote', note.id)" :search="search" :lang="lang" />
    </div>
  </div>
</template>

<script>
import NotesItem from "@/components/NotesItem.vue";
export default {
  components: {
    NotesItem,
  },
  props: {
    notes: Array,
    search: String,
    lang: String,
  },
  inject: ['words'],
  data() {
    return {
      changer: false,
      flex: "changer__flex",
      grid: "changer__grid",
    };
  },
};
</script>
