<template>
  <div class="notes__item">
    <div class="notes__item-top">
      <h3 class="notes__item-title">{{ note.title }}</h3>
      <p class="notes__item-date">{{ note.data }}</p>
    </div>
    <p class="notes__item-desc">
      {{ note.desc }}
    </p>
    <div class="notes__item-btns" v-if="!search">
      <button class="notes__item-btn redact" @click="$event => $emit('changeNote', note.id)">
        <img src="@/assets/img/pen.svg" alt="" />
        <span>{{ words.editbtn[lang] }}</span>
      </button>
      <button class="notes__item-btn delete" @click="$emit('delNote', note.id)">
        <img src="@/assets/img/trash.svg" alt="" />
        <span>{{ words. deledit[lang] }}</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  inject: ['words'],
  props: {
    note: {
      typeof: Object,
      default: {
        title: "",
        desc: "",
        date: "",
        id: 0,
      },
    },
    search: String,
    lang:String,
  },
};
</script>

<style></style>