<template>
  <Navbar @getSearch="search = $event" :lang="lang" @changeLang="changeLang" />
  <div class="container">
    <Notes :notes="filterNotes" :search="search" @changeNote="changeNote" @delNote="delNote" @changeLang="changeLang"
      :lang="lang" />
  </div>
  <AddBtn @openModal="openModal" v-if="!search" />
  <Modal v-show="modalOpen" :edit="edit" @closeModal="closeModal" :currentId="currentId" @addNote="addNote"
    :editNote="editNote" @editedNote="editedNote" :lang="lang" />
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Notes from "@/components/Notes.vue";
import AddBtn from "@/components/AddBtn.vue";
import Modal from "@/components/Modal.vue";
import langs from '@/lang'
export default {
  name: "Todo",
  components: {
    Navbar,
    Notes,
    AddBtn,
    Modal,
  },
  data() {
    return {
      search: '',
      modalOpen: false,
      edit: false,
      notes: [
        //{ id: 1, title: "Vue", desc: "lorem ipsum", data: "16.04.2023" },//
        //{ id: 2, title: "React", desc: "lorem ipsum", data: "16.04.2023" },//
        //{ id: 3, title: "Angular", desc: "lorem ipsum", data: "16.04.2023" },//
      ],
      editNote: {},
      currentId: 1,
      langwords: {},
      lang: 'ru',
    };
  },

  methods: {
    changeLang(val) {
      this.lang = localStorage.lang = val;
    },
    openModal() {
      this.modalOpen = true;
      this.edit = false;
    },
    closeModal(status) {
      this.modalOpen = status;
    },
    addNote(item) {
      if (item.title != '' && item.desc != '') {
        this.notes.push(item)
      }
    },
    changeNote(id) {
      this.edit = this.modalOpen = true
      let pickedNote = this.notes.find((note) => note.id === id)
      this.editNote = pickedNote
    },
    editedNote(noteEdited) {
      this.notes.forEach(note => {
        if (note.id == noteEdited.id) {
          note.title = noteEdited.title
          note.desc = noteEdited.desc
          note.data = noteEdited.data
        }
      })
    },
    delNote(id) {
      let index = this.notes.findIndex(note => note.id == id)
      this.notes.splice(index, 1);
    },
    getNotes() {
      const localNotes = localStorage.notes
      if (localNotes) {
        this.notes = JSON.parse(localNotes)
      }
    },

  },
  computed: {
    filterNotes() {
      return this.search ? this.notes.filter(item => item.title.toLowerCase().includes(this.search.toLowerCase())) : this.notes
    },
  },
  watch: {
    notes: {
      handler() {
        localStorage.notes = JSON.stringify(this.notes)
      },
      deep: true,
    }
  },
  created() {
    this.getNotes()
    localStorage.lang = localStorage.lang || 'ru'
    this.lang = localStorage.lang || 'ru'
    this.langwords = langs
    localStorage.words = JSON.stringify(this.langwords)
  },
  provide() {
    return {
      words: localStorage.words ? JSON.parse(localStorage.words) : langs
    }
  }
};
</script>

